export default class FormTemplate {
    input(name, value = null, attributes = {}) {
        let input = document.createElement('input');
        input.name = name;
        if (value) {
            input.value = value;
        }
        attributes = Object.assign({}, {
            type: 'text'
        }, attributes);
        this.setHtmlAttributes(input, attributes);
        return input;
    }
    textarea(name, value = '', attributes = {}) {
        let textarea = document.createElement('textarea');
        textarea.name = name;
        this.setHtmlAttributes(textarea, attributes);
        return textarea;
    }
    checkbox(name, value, label) {
        let input = document.createElement('label');
        input.classList.add('input__checkbox');
        input.innerHTML = `<input type="checkbox" name="${name}" class="input__checkbox__input" value="${value}"><span class="input__checkbox__label">${label}</span>`;
        return input;
    }
    setHtmlAttributes(input, attributes) {
        Object.keys(attributes).forEach(attribute => input.setAttribute(attribute, attributes[attribute]));
    }
    inputGroup(label, input) {
        return `
		<div class="input__group">
			<label>${label}</label>
			<div class="input__controlgroup">${input}</div>
		</div>
		`;
    }
}
