import AbstractEditable from './abstract';
export default class TextareaEditable extends AbstractEditable {
    getEditableType() {
        return "textarea";
    }
    focus() {
        this.getElement().querySelector('textarea').focus({ preventScroll: true });
    }
    valueToInput(value) {
        const textarea = this.getElement().querySelector('textarea');
        setTimeout(() => {
            textarea.focus();
            textarea.setSelectionRange(0, 0);
        }, 0);
        if (value) {
            textarea.value = value;
        }
    }
    getValue() {
        return this.getElement().querySelector('textarea').value;
    }
    getTemplate() {
        return this.getEditableTemplate().getFormHelper().textarea('text', null, this.settings.html_attributes).outerHTML;
    }
    getDefaults() {
        return Object.assign(super.getDefaults(), {
            html_attributes: {
                rows: 6
            }
        });
    }
}
