export function getColumnValueFromRowHTML(rowHTML, column) {
    let row = document.createElement('tr');
    column = column.replace(/\./g, '\\.');
    row.innerHTML = rowHTML;
    let col = row.querySelector(`td.column-${column}`);
    if (col) {
        let invalidElements = col.querySelectorAll('.row-actions');
        if (invalidElements) {
            invalidElements.forEach((element) => {
                element.remove();
            });
        }
        return col.innerHTML;
    }
    return '';
}
