import Modal from "AC/modules/modal";
import Nanobus from "nanobus";
const i18n = {
    AFFECT: 'This will affect <strong><span data-total="">12.500</span> entries</strong>'
};
export default class BulkEditModal extends Modal {
    constructor(Editable) {
        super(createModalHtml());
        this.canDismiss = true;
        this.Editable = Editable;
        this.events = new Nanobus();
        this.hideProcess();
        this.setEditable(this.Editable.get());
    }
    close() {
        if (!this.canDismiss) {
            return;
        }
        super.close();
        this.events.emit('close');
    }
    setTotal(number) {
        this.el.querySelectorAll('[data-total] ').forEach((el) => {
            el.innerHTML = number;
        });
    }
    setEditable(el) {
        this.el.querySelector('[data-editable]').append(el);
    }
    setTitle(title) {
        this.el.querySelector('[data-title]').innerHTML = title;
    }
    setProcess(el) {
        let container = this.el.querySelector('[data-process]');
        this.el.querySelector('.ac-modal__dialog__content.-info').style.display = 'none';
        container.innerHTML = '';
        container.append(el);
        container.style.display = 'block';
        this.canDismiss = false;
        this.toggleCloseButton(false);
    }
    setCanDismiss(canDismiss) {
        this.canDismiss = canDismiss;
    }
    toggleCloseButton(show) {
        this.el.querySelector('.ac-modal__dialog__close').style.display = show ? 'block' : 'none';
    }
    hideProcess() {
        this.el.querySelector('[data-process]').style.display = 'none';
    }
}
const createModalHtml = () => {
    let modal = document.createElement('div');
    modal.classList.add('ac-modal');
    modal.classList.add('-bulkedit');
    modal.innerHTML = `
				<div class="ac-modal__dialog">
					<div class="ac-modal__dialog__header">
						<span data-title></span>
						<button class="ac-modal__dialog__close">
							<span class="dashicons dashicons-no"></span>
						</button>
					</div>
					<div class="ac-modal__dialog__content -editable" data-editable></div>
					<div class="ac-modal__dialog__content -info">
						<span class="acp-be-warning__icon dashicons dashicons-info"></span><em>${i18n.AFFECT}</em> 
					</div>
					<div class="ac-modal__dialog__content -process" data-process>
					
					</div>
					<div class="ac-modal__dialog__content -log">
					    <div data-log></div>
					</div>
				</div>
			`;
    document.body.appendChild(modal);
    return modal;
};
