import AbstractEditable from './abstract';
export default class Select2RemoteEditable extends AbstractEditable {
    getEditableType() {
        return 'select';
    }
    close() {
        jQuery(this.getElement().querySelector('.acs2')).ac_select2('close');
        return super.close();
    }
    valueToInput(value) {
        this.getRemoteData().done((e) => {
            let args = this.settings.select2;
            args.data = e.data.results;
            jQuery(this.getElement().querySelector('.acs2')).ac_select2(args).val(value).trigger('change');
        });
    }
    getRemoteData() {
        const objectId = this.getObjectId();
        return jQuery.ajax({
            url: ajaxurl,
            dataType: 'json',
            method: 'get',
            data: {
                action: 'acp_editing_request',
                method: this.settings.mode + '-select-values',
                layout: AC.layout,
                column: this.settings.column,
                item_id: objectId ? objectId : null,
                list_screen: AC.list_screen,
                _ajax_nonce: AC.ajax_nonce
            }
        });
    }
    getValue() {
        return this.getElement().querySelector('select.acs2').value;
    }
    getTemplate() {
        return `<select class="acs2"></select>`;
    }
    getDefaults() {
        let defaults = super.getDefaults();
        defaults['showbuttons'] = true;
        return defaults;
    }
}
