import Abstract from 'ACP/Editing/middleware/editable/abstract';
import * as Defaults from 'ACP/Editing/middleware/editable/default';
import Select2 from 'ACP/Editing/middleware/editable/select2';
import Select2Remote from "ACP/Editing/middleware/editable/select2_remote";
import Taxonomy from 'ACP/Editing/middleware/editable/taxonomy';
import Media from 'ACP/Editing/middleware/editable/media';
import MultiInput from 'ACP/Editing/middleware/editable/multi_input';
import Fullname from "ACP/Editing/middleware/editable/fullname";
import NumberExtended from "ACP/Editing/middleware/editable/number_extended";
import { EDIT_MODES, EDIT_SERVICES } from "ACP/Editing/const";
import { Link } from "ACP/Editing/middleware/editable/link";
export default class EditableMiddleware {
    constructor(editables, services) {
        this.services = services;
        this.Editables = editables;
        this.middleWareClasses = {
            '_abstract': Abstract,
            'default': Defaults.Default,
            'text': Defaults.Default,
            'internal_link': Link,
            'url': Defaults.Url,
            'number': Defaults.Number,
            'number_extended': NumberExtended,
            'password': Defaults.Password,
            'email': Defaults.Email,
            'checklist': Defaults.Checkbox,
            'color': Defaults.Color,
            'select': Defaults.Select,
            'select2_dropdown': Select2,
            'select2_remote': Select2Remote,
            'date': Defaults.Date,
            'date_time': Defaults.DateTime,
            'textarea': Defaults.Textarea,
            'togglable': Defaults.Togglable,
            'attachment': Media,
            'media': Media,
            'float': Defaults.Float,
            'multi_input': MultiInput,
            'types_multi_input': MultiInput,
            'taxonomy': Taxonomy,
            'wysiwyg': Defaults.Wysiwyg,
            'fullname': Fullname,
        };
        this.services.emitEvent('Editing.Middleware.Ready', this);
    }
    register(name, object) {
        this.middleWareClasses[name] = object;
        return this;
    }
    getClass(type) {
        return this.middleWareClasses.hasOwnProperty(type)
            ? this.middleWareClasses[type]
            : this.middleWareClasses['default'];
    }
    getInlineEditable(column) {
        const editable = column.getService(EDIT_SERVICES.INLINE_EDITABLE);
        const type = editable.type;
        const middleware = this.middleWareClasses.hasOwnProperty(type)
            ? this.middleWareClasses[type]
            : this.middleWareClasses['default'];
        return new middleware(this.Editables, editable, column, EDIT_MODES.INLINE);
    }
    getBulkEditable(column) {
        let editable = column.getService(EDIT_SERVICES.BULK_EDITABLE);
        let type = editable.type;
        let middleware = this.middleWareClasses.hasOwnProperty(type)
            ? this.middleWareClasses[type]
            : this.middleWareClasses['default'];
        return new middleware(this.Editables, editable, column, EDIT_MODES.BULK);
    }
}
