import AbstractEditable from './abstract';
import { valueToOptions } from "ACP/Editing/helpers/object";
const i18n = {
    REPLACE: ACP_Editing.i18n.replace_with,
    ADD: ACP_Editing.i18n.add,
    REMOVE: ACP_Editing.i18n.remove
};
class AbstractSelect2Ajax extends AbstractEditable {
    getEditableType() {
        return 'select2';
    }
    focus() {
        jQuery(this.getSelectBox()).ac_select2('open');
    }
    render() {
        if (this.getObjectId()) {
            this.getElement().querySelector('.acs2').dataset.objectId = this.getObjectId();
        }
        if (!this.settings.showbuttons) {
            this.getElement().querySelector('.aceditable__form__controls').style.display = 'none';
        }
    }
    getSelectBox() {
        return this.getElement().querySelector('.acs2');
    }
    close() {
        jQuery(this.getElement().querySelector('.acs2')).ac_select2('close');
        return super.close();
    }
    valueToInput(value) {
        let $select = jQuery(this.getElement().querySelector('.acs2'));
        $select.ac_select2(this.settings.select2);
        if (value) {
            valueToOptions(value, true).forEach(option => $select.append(option).trigger('change'));
        }
        if (!this.settings.showbuttons) {
            $select.on('change', () => {
                this.save();
            });
        }
    }
    getTemplate() {
        return `<select class="acs2"></select>`;
    }
}
export class Select2Ajax extends AbstractSelect2Ajax {
    getValue() {
        return this.getElement().querySelector('select.acs2').value;
    }
}
export class Select2AjaxMultiple extends AbstractSelect2Ajax {
    getValue() {
        const selected = this.getElement().querySelectorAll('select.acs2 option:checked');
        return Array.from(selected).map((option) => option.value);
    }
}
export class Select2AjaxMultipleStrategy extends Select2AjaxMultiple {
    render() {
        super.render();
        this.getElement().querySelector('.aceditable__form__inputs').insertAdjacentHTML("afterbegin", TEMPLATE);
    }
    getTypeElement() {
        return this.getElement().querySelector('[name=method]');
    }
    getValue() {
        return {
            value: super.getValue(),
            method: this.getTypeElement().value
        };
    }
}
export const TEMPLATE = `
	<div class="input__group -type -strategy">
        <select class="type" name="method">
            <option value="replace">${i18n.REPLACE}..</option>
            <option value="add">${i18n.ADD}..</option>
            <option value="remove">${i18n.REMOVE}..</option>
        </select>
	</div>
`;
