import Abstract from './abstract';
export default class Select2Remote extends Abstract {
    getEditable() {
        return this.Editables.get('select2_remote');
    }
    map() {
        this.args.column = this.column.name;
        this.args.select2 = {
            width: 320,
            theme: 'acs2',
            escapeMarkup: (text) => jQuery('<div>' + text + '</div>').text()
        };
    }
}
