import AbstractEditable from './abstract';
export default class DateEditable extends AbstractEditable {
    getEditableType() {
        return "date";
    }
    focus() {
        return;
    }
    render() {
        const self = this;
        const $input = jQuery(this.getElement().querySelector('[name=date]'));
        document.body.classList.add('ac-jqui');
        $input.prop('readonly', true).datepicker({
            dateFormat: 'yy-mm-dd',
            changeYear: true,
            firstDay: this.settings.weekstart,
            showButtonPanel: true,
            onSelect: () => {
                if (!this.settings.showbuttons) {
                    self.save();
                }
            },
            beforeShow: (input, inst) => {
                setTimeout(function () {
                    let datepicker = inst.dpDiv.get(0);
                    datepicker.style.position = 'relative';
                    datepicker.style.left = 0;
                    datepicker.style.top = 0;
                    self.getElement().querySelector('.input__date').append(datepicker);
                }, 0);
            }
        }).on('click', () => {
            $input.prop('readonly', false);
        }).on('blur', () => {
            $input.prop('readonly', true);
        });
    }
    valueToInput(value) {
        if (value && value.length === 8) {
            value = mapACFDateFormat(value);
            setTimeout(() => {
                jQuery(this.getElement().querySelector('[name=date]')).val(value).datepicker('show');
            }, 0);
        }
    }
    getValue() {
        return this.getElement().querySelector('[name=date]').value;
    }
    getTemplate() {
        let input = this.getEditableTemplate().getFormHelper().input('date', '', { placeholder: 'yyyy-mm-dd', autocomplete: 'nope' }).outerHTML;
        return `
			${input}
			<div class="input__date"></div>
		`;
    }
    getDefaults() {
        let defaults = super.getDefaults();
        defaults['weekstart'] = 1;
        return defaults;
    }
}
const mapACFDateFormat = (date) => {
    date = [date.slice(0, 4), '-', date.slice(4)].join('');
    date = [date.slice(0, 7), '-', date.slice(7)].join('');
    return date;
};
