import Abstract from "./abstract";
export default class Media extends Abstract {
    getEditable() {
        return this.Editables.get('wp_library');
    }
    map() {
        this.args.showbuttons = false;
        this.args.multiple = !!(this.settings.hasOwnProperty('multiple') && this.settings.multiple);
        if (this.settings.hasOwnProperty('attachment') && this.settings.attachment.hasOwnProperty('library')) {
            this.args.library = this.settings.attachment.library;
        }
        return this.args;
    }
    hasMediaActions() {
        return true;
    }
}
