import Abstract from './abstract';
import { formatOptionsSelect2 } from "ACP/Editing/modules/helper";
export class Default extends Abstract {
    getEditable() {
        return this.Editables.get('text');
    }
    map() {
        this.mapHTMLAttributes(this.settings);
        return this.args;
    }
    mapHTMLAttributes(editable) {
        let attributes = {};
        if (editable.hasOwnProperty('required')) {
            attributes.required = editable.required;
        }
        if (editable.hasOwnProperty('placeholder') && editable.placeholder !== null) {
            attributes.placeholder = editable.placeholder;
        }
        if (editable.hasOwnProperty('range_max')) {
            attributes.max = editable.range_max;
        }
        if (editable.hasOwnProperty('range_min')) {
            attributes.min = editable.range_min;
        }
        if (editable.hasOwnProperty('range_step')) {
            attributes.step = editable.range_step;
        }
        if (editable.hasOwnProperty('maxlength')) {
            attributes.maxlength = editable.maxlength;
        }
        if (editable.hasOwnProperty('rows')) {
            attributes.rows = editable.rows;
        }
        this.args.html_attributes = attributes;
    }
}
export class Email extends Default {
    map() {
        super.map();
        this.args.html_attributes.type = 'email';
        return this.args;
    }
}
export class Number extends Default {
    map() {
        super.map();
        this.args.html_attributes.type = 'number';
        return this.args;
    }
}
export class Url extends Default {
    map() {
        super.map();
        this.args.html_attributes.type = 'url';
        return this.args;
    }
}
export class Textarea extends Default {
    getEditable() {
        return this.Editables.get('textarea');
    }
}
export class Float extends Default {
    map() {
        super.map();
        this.args.html_attributes.type = 'number';
        this.args.html_attributes.step = 'any';
        return this.args;
    }
}
export class Password extends Default {
    map() {
        super.map();
        this.args.html_attributes.type = 'password';
        return this.args;
    }
}
export class Color extends Abstract {
    getEditable() {
        return this.Editables.get('color');
    }
}
export class Wysiwyg extends Abstract {
    getEditable() {
        return this.Editables.get('wysiwyg');
    }
    map() {
        return this.args;
    }
}
export class Date extends Abstract {
    getEditable() {
        return this.Editables.get('date');
    }
    map() {
        this.args.showbuttons = false;
        return this.args;
    }
}
export class DateTime extends Abstract {
    map() {
        this.args.timeformat = this.settings.timeformat;
        return this.args;
    }
    getEditable() {
        return this.Editables.get('date_time');
    }
}
export class Checkbox extends Abstract {
    map() {
        this.args.options = formatOptionsSelect2(this.settings.options);
        return this.args;
    }
    getEditable() {
        return this.Editables.get('checklist');
    }
}
export class Select extends Abstract {
    getEditable() {
        return this.Editables.get('select');
    }
    map() {
        this.args.options = this.settings.options;
        return this.args;
    }
}
export class Togglable extends Abstract {
    getEditable() {
        return this.Editables.get('toggle');
    }
    map() {
        this.args.options = this.settings.options;
        this.args.showbuttons = false;
        return this.args;
    }
    flattenOptionArray(options) {
        let result = [];
        options.forEach((option) => {
            result.push(option.label);
        });
        return result;
    }
}
