import Nanobus from "nanobus";
import { EDIT_SERVICES } from "ACP/Editing/const";
const SELECTORS = {
    ROWCLASS: 'acp-be-editrow'
};
const i18n = {
    BULK_EDIT: ACP_Editing.i18n.bulk_edit.bulk_edit
};
export default class BulkEditRow {
    constructor(BulkEdit) {
        this.BulkEdit = BulkEdit;
        this.Table = BulkEdit.Table;
        this.Middleware = BulkEdit.Middleware;
        this.events = new Nanobus();
    }
    add() {
        let thead = this.Table.getElement().getElementsByTagName('thead')[0];
        let headers = thead.children[0].children;
        let row = document.createElement('tr');
        row.classList.add(SELECTORS.ROWCLASS);
        for (let i = 0; i < headers.length; i++) {
            let header = headers[i];
            let column = this.Table.Columns.get(header.id);
            let cell = document.createElement('td');
            cell.classList.add(`column-${header.id}`);
            if (header.id !== 'cb' && column && column.hasService(EDIT_SERVICES.BULK_EDITABLE)) {
                let editable = column.getService(EDIT_SERVICES.BULK_EDITABLE);
                if (editable === null) {
                    row.appendChild(cell);
                    continue;
                }
                cell.innerHTML = `<button class="button" data-name="${header.id}">${i18n.BULK_EDIT}</button>`;
                this.initEvents(cell);
            }
            row.appendChild(cell);
        }
        this.Table.getElement().querySelector('tbody').insertAdjacentElement('afterbegin', row);
        this.refresh();
        return row;
    }
    remove() {
        this.Table.getElement().querySelectorAll(`.${SELECTORS.ROWCLASS}`).forEach(el => el.remove());
    }
    initEvents(cell) {
        cell.querySelector('button').addEventListener('click', (e) => {
            e.preventDefault();
            e.stopPropagation();
            this.events.emit('click', { name: e.target.dataset.name });
        }, true);
    }
    onButtonClick(callback) {
        this.events.addListener('click', callback);
    }
    getRow() {
        return this.Table.getElement().querySelector(`.${SELECTORS.ROWCLASS}`);
    }
    refresh() {
        let thead = this.Table.getElement().getElementsByTagName('thead')[0];
        let headers = thead.children[0].children;
        for (let i = 0; i < headers.length; i++) {
            let header = headers[i];
            let col = this.getRow().querySelector(`.column-${header.id}`);
            if (!col) {
                continue;
            }
            header.classList.contains('hidden')
                ? col.classList.add('hidden')
                : col.classList.remove('hidden');
        }
    }
}
