import AbstractEditable from './abstract';
export default class DateTimeEditable extends AbstractEditable {
    getEditableType() {
        return "datetime";
    }
    render() {
        document.body.classList.add('ac-jqui');
        DateTimeEditable.createSelectHours(this.getHourElement(), this.getArg('timeformat', null));
        DateTimeEditable.createSelectIncementalOptions(this.getMinutesElement());
        DateTimeEditable.createSelectIncementalOptions(this.getSecondsElement());
        jQuery(this.getDateElement()).datepicker({
            dateFormat: 'yy-mm-dd',
            changeYear: true,
            showButtonPanel: true,
            firstDay: this.getArg('weekstart', 1),
        });
    }
    close() {
        document.body.classList.remove('ac-jqui');
        return super.close();
    }
    focus() {
        this.getDateElement().focus({ preventScroll: true });
    }
    valueToInput(value) {
        if (!value) {
            return;
        }
        let DateTime = new Date(value.replace(/-/g, "/"));
        if (isNaN(DateTime.getTime())) {
            return;
        }
        this.getDateElement().value = DateTime.getFullYear() + '-' + ('0' + (DateTime.getMonth() + 1)).slice(-2) + '-' + ('0' + DateTime.getDate()).slice(-2);
        this.getHourElement().value = ('0' + (DateTime.getHours())).slice(-2);
        this.getMinutesElement().value = ('0' + (DateTime.getMinutes())).slice(-2);
        this.getSecondsElement().value = ('0' + (DateTime.getSeconds())).slice(-2);
    }
    getValue() {
        let date = this.getDateElement().value;
        let hours = this.getHourElement().value;
        let minutes = this.getMinutesElement().value;
        let seconds = this.getSecondsElement().value;
        if (!date || !hours || !minutes || !seconds) {
            return false;
        }
        return `${date} ${hours}:${minutes}:${seconds}`;
    }
    getTemplate() {
        let input = this.getEditableTemplate().getFormHelper().input('date', '', { placeholder: 'yyyy-mm-dd', autocomplete: 'nope' }).outerHTML;
        return `
			${input}
			<div class="input__time">
				<select name="time_hours"></select>				
				<select name="time_minutes"></select>				
				<select name="time_seconds"></select>				
			</div>
		`;
    }
    getDateElement() {
        return this.getElement().querySelector('[name=date]');
    }
    getHourElement() {
        return this.getElement().querySelector('[name=time_hours]');
    }
    getMinutesElement() {
        return this.getElement().querySelector('[name=time_minutes]');
    }
    getSecondsElement() {
        return this.getElement().querySelector('[name=time_seconds]');
    }
    getDefaults() {
        return Object.assign(super.getDefaults(), {
            'weekstart': 1,
            'showbuttons': true
        });
    }
    static createSelectHours(select, time_format) {
        for (let number = 0; number < 24; number++) {
            let option = document.createElement('option');
            let _number = ('0' + number.toString()).slice(-2);
            option.setAttribute('value', _number);
            option.text = (12 === time_format) ? DateTimeEditable.formatAMPM(parseInt(_number)) : _number;
            select.append(option);
        }
    }
    static formatAMPM(hours) {
        let ampm = hours >= 12 ? 'pm' : 'am';
        hours = hours % 12;
        hours = hours ? hours : 12; // the hour '0' should be '12'
        return hours + ' ' + ampm;
    }
    static createSelectIncementalOptions(select, start = 0, end = 60) {
        for (let number = start; number < end; number++) {
            let option = document.createElement('option');
            let _number = ('0' + number).slice(-2);
            option.setAttribute('value', _number);
            option.text = _number;
            select.append(option);
        }
    }
}
