import Abstract from './abstract';
export default class Fullname extends Abstract {
    getEditable() {
        return this.Editables.get('fullname');
    }
    map() {
        this.args.placeholder_first_name = this.settings.placeholder_first_name;
        this.args.placeholder_last_name = this.settings.placeholder_last_name;
        return this.args;
    }
}
