import AbstractEditable from './abstract';
import { getNestedValue } from "ACP/Editing/helpers/object";
export default class TextEditable extends AbstractEditable {
    getEditableType() {
        return "text";
    }
    render() {
        this.getElement().classList.add(`-input-${getNestedValue(this.settings, 'text', ['html_attributes', 'type'])}`);
    }
    focus() {
        this.getElement().querySelector('input').focus({ preventScroll: true });
    }
    valueToInput(value) {
        if (value) {
            this.getElement().querySelector('input').value = value;
        }
    }
    getValue() {
        return this.getElement().querySelector('input').value;
    }
    getTemplate() {
        return this.getEditableTemplate().getFormHelper().input('input', null, this.settings.html_attributes).outerHTML;
    }
}
