export default class BulkLogger {
    constructor() {
        this.clear();
    }
    getErrors() {
        return this.data.filter(r => r.status === 'failed');
    }
    getNotEditable() {
        return this.data.filter(r => r.status === 'not_editable');
    }
    hasError() {
        return this.getErrors().length > 0;
    }
    hasNotEditable() {
        return this.getNotEditable().length > 0;
    }
    add(data) {
        this.data = [...this.data, ...data];
    }
    clear() {
        this.data = [];
        return this;
    }
}
