import SelectEditable from "ACP/Editing/editable/select";
import { TEMPLATE } from "ACP/Editing/editable/select2_ajax";
export class Select2Editable extends SelectEditable {
    render() {
        super.render();
        jQuery(this.getSelectElement()).ac_select2(this.settings.select2).ac_select2('focus');
        if (!this.settings.showbuttons) {
            this.getElement().querySelectorAll('.aceditable__form__controls').forEach(el => el.style.display = 'none');
            jQuery(this.getSelectElement()).on('change', () => {
                this.save();
            });
        }
    }
    close() {
        jQuery(this.getSelectElement()).ac_select2('close');
        return super.close();
    }
    valueToInput(value) {
        jQuery(this.getSelectElement()).val(value).change();
    }
}
export class Select2Multiple extends Select2Editable {
    // @ts-ignore
    valueToInput(value) {
        jQuery(this.getSelectElement()).val(value).change();
    }
    // @ts-ignore
    getValue() {
        const selected = this.getSelectElement().querySelectorAll('option:checked');
        return Array.from(selected).map(el => el.value);
    }
}
export class Select2MultipleMethods extends Select2Multiple {
    render() {
        super.render();
        this.getElement().querySelector('.aceditable__form__inputs').insertAdjacentHTML("afterbegin", TEMPLATE);
    }
    getValue() {
        return {
            value: super.getValue(),
            method: this.getElement().querySelector('[name=method]').value
        };
    }
}
