export const getNestedValue = (obj, defaultValue, ...args) => {
    let value = args.reduce((obj, level) => obj && obj[level], obj);
    return typeof value !== 'undefined' ? value : defaultValue;
};
export const evalValue = (value, defaultValue) => {
    return typeof value === 'undefined' ? defaultValue : value;
};
export const parseArguments = (args, defaultArgs) => {
    let result = defaultArgs;
    if (typeof args === 'object') {
        Object.keys(args).forEach((key) => {
            let value = args[key];
            if (typeof result === 'undefined') {
                result = {};
            }
            if (typeof value === 'object') {
                if (!result.hasOwnProperty(key)) {
                    result[key] = Array.isArray(value) ? [] : {};
                }
                result[key] = parseArguments(value, result[key]);
            }
            else {
                result[key] = value;
            }
        });
    }
    return result;
};
export const valueToOptions = (valueObject, sorted = true) => {
    const options = [];
    Object.keys(valueObject).forEach(v => {
        options.push(new Option(valueObject[v], v, true, true));
    });
    if (sorted) {
        options.sort((a, b) => {
            return a.label.localeCompare(b.label);
        });
    }
    return options;
};
