import AbstractEditable from './abstract';
import { insertAfter } from "AC/helpers/elements";
export default class MultiInputEditable extends AbstractEditable {
    getEditableType() {
        return "multi_input";
    }
    focus() {
        let input = this.getElement().querySelector('form input, form textarea');
        if (input) {
            input.focus({ preventScroll: true });
        }
    }
    getSubType() {
        return this.getArg('subtype');
    }
    valueToInput(values) {
        if (!values || (Array.isArray(values) && values.length === 0)) {
            values = [''];
        }
        values.forEach((value) => {
            let input = this.createSingleInput(value);
            this.getElement().querySelector('.acp-ie-multi-container').append(input);
        });
    }
    createSingleInput(value) {
        let subtype = this.settings.subtype;
        let container = document.createElement('div');
        let input = subtype === 'textarea'
            ? this.getEditableTemplate().getFormHelper().textarea('input', value)
            : this.getEditableTemplate().getFormHelper().input('input', value, { type: this.getSubType() });
        input.value = value;
        container.classList.add('single-input');
        container.append(input);
        container.append(this.createControls());
        return container;
    }
    determineControls() {
        let lines = this.getElement().querySelectorAll('.single-input').length;
        this.getElement().querySelectorAll('.single-input__control.-remove').forEach(el => {
            el.style.display = lines > 1 ? 'inline-block' : 'none';
        });
    }
    createControls() {
        let controls = document.createElement('div');
        controls.classList.add('single-input__controls');
        controls.innerHTML = `
			<span class="single-input__control -add">
				<span class="dashicons dashicons-plus"></span>
			</span>
			<span class="single-input__control -remove">
				<span class="dashicons dashicons-minus"></span>
			</span>
		`;
        controls.querySelector('.-add').addEventListener('click', (e) => this.eventNewRow(e.target));
        controls.querySelector('.-remove').addEventListener('click', (e) => {
            e.target.closest('.single-input').remove();
            this.determineControls();
        });
        setTimeout(() => {
            this.determineControls();
        }, 0);
        return controls;
    }
    eventNewRow(element) {
        let input = element.closest('.single-input');
        if (input) {
            insertAfter(this.createSingleInput(''), input);
        }
    }
    getValue() {
        let value = [];
        this.getElement().querySelectorAll('.single-input input, .single-input textarea').forEach((input) => {
            value.push(input.value);
        });
        return value;
    }
    getTemplate() {
        return `<div class="acp-ie-multi-container"></div>`;
    }
    getDefaults() {
        return Object.assign(super.getDefaults(), {
            'subtype': 'text'
        });
    }
}
