import AbstractEditable from './abstract';
import PostLinks from "./components/PostLinks.svelte";
export default class LinkEditable extends AbstractEditable {
    constructor(args) {
        super(args);
        this.dynamicValue = '';
    }
    getEditableType() {
        return "internal_link";
    }
    valueToInput(value) {
        this.dynamicValue = value;
        let container = this.getElement().querySelector('[data-component]');
        new PostLinks({
            target: container,
            props: {
                value: value,
                onChange: (value) => {
                    this.dynamicValue = value;
                }
            }
        });
    }
    getValue() {
        return this.dynamicValue;
    }
    getTemplate() {
        return `
            <div data-component></div>
        `;
    }
}
