import { getColumnValueFromRowHTML } from "../helpers/rows";
import { AJAX_VARS, EDIT_SERVICES } from "ACP/Editing/const";
export default class TableUpdate {
    constructor(Table) {
        this.Table = Table;
    }
    updateSelectedCells(column, value = '') {
        let ids = this.Table.Selection.getIDs();
        if (ids.length === 0) {
            return;
        }
        this.getRows(ids).done((response) => {
            if (!response.hasOwnProperty('data') || !response.data.hasOwnProperty('table_rows')) {
                return;
            }
            let rows = response.data.table_rows;
            Object.keys(rows).forEach((id) => {
                this.Table.Cells.get(parseInt(id), column).setValue(getColumnValueFromRowHTML(rows[id], column));
            });
            this.updateSelectedEditingCells(column, rows, value);
        });
    }
    updateSelectedEditingCells(column, rows, value) {
        let ids = this.Table.Selection.getIDs();
        if (ids.length === 0) {
            return;
        }
        let Editing_items = jQuery.ajax({
            url: ajaxurl,
            method: 'post',
            data: {
                action: AJAX_VARS.ACTION,
                method: AJAX_VARS.METHOD_INLINE_VALUES,
                ids: ids,
                column: column,
                list_screen: AC.list_screen,
                layout: AC.layout,
                _ajax_nonce: AC.ajax_nonce
            }
        });
        Editing_items.done((response) => {
            response.data.editable_values.forEach((setting) => {
                if (setting.column_name !== column) {
                    return;
                }
                let cell = this.Table.Cells.get(setting.id, column);
                let iEdit = cell.getService('IEdit');
                cell.setValue(getColumnValueFromRowHTML(rows[setting.id], setting.column_name));
                if (iEdit) {
                    cell.getService(EDIT_SERVICES.DATA_STORAGE).storeRevision(value);
                    iEdit.Editable.setValue(setting.value);
                }
            });
        });
    }
    getRows(ids) {
        return jQuery.ajax({
            url: window.location.href,
            method: 'post',
            data: {
                ac_action: 'get_table_rows',
                ac_ids: ids,
                _ajax_nonce: AC.ajax_nonce
            }
        });
    }
}
