import { Actions, MediaActions } from "./inline-edit-actions";
import Popper from 'popper.js';
import { getColumnValueFromRowHTML } from "ACP/Editing/helpers/rows";
import { insertAfter } from "AC/helpers/elements";
import { getTableRows } from "ACP/Editing/helpers/ajax";
import { EDIT_SERVICES } from "ACP/Editing/const";
import { initAcServices } from "AC/helpers/admin-columns";
const Selectors = {
    CELL: 'acp-ie-editable',
    VALUE: 'acp-ie-value'
};
export default class InlineEditCell {
    constructor(InlineEdit, Cell, Middleware, editable_class) {
        this.EditableConstructor = editable_class;
        this.InlineEdit = InlineEdit;
        this.Cell = Cell;
        this.Middleware = Middleware;
        this.wrapCellContent();
        this.attachEditable();
        this.insertControls();
        this.attachEvents();
    }
    getDataStorage() {
        return this.Cell.getService(EDIT_SERVICES.DATA_STORAGE);
    }
    getEditableSettings() {
        return this.Cell.getSettings().getService(EDIT_SERVICES.INLINE_EDITABLE);
    }
    attachEditable() {
        if (!this.Cell.hasService(EDIT_SERVICES.DATA_STORAGE) || this.Editable) {
            return;
        }
        let value = this.getDataStorage().getFormattedValue();
        if (value === null) {
            return;
        }
        this.Editable = new this.EditableConstructor(this.Middleware.getArgs());
        this.Editable.setValue(value);
        if (this.Cell.getObjectID()) {
            this.Editable.setObjectId(this.Cell.getObjectID());
        }
        this.Editable.Events.addListener('save', (editable) => {
            this.saveValue(editable.getValue());
        });
        this.Editable.Events.addListener('close', () => {
            this.setFocusOnRelatedCell();
            PopperGlobalEvents.destroy();
            this.destroyPopper();
        });
    }
    setFocusOnRelatedCell() {
        let el = this.Cell.getElement().querySelector('.-edit');
        if (el) {
            this.Cell.getElement().closest('tr').classList.add('forced-focus');
            el.focus();
            this.Cell.getElement().closest('tr').classList.remove('forced-focus');
        }
    }
    attachEvents() {
        this.Cell.events.addListener('setValue', () => {
            this.afterSetValue();
        });
        this.getTargetElement().addEventListener('click', (e) => {
            if (!this.InlineEdit.isEnabled() || e.target.tagName === 'A') {
                return;
            }
            e.preventDefault();
            e.stopPropagation();
            this.openEditable();
        });
        document.addEventListener('ACP_InlineEditing_Close', () => {
            if (this.Popper) {
                this.Editable.close();
            }
        });
    }
    saveValue(value) {
        this.Editable.toggleLoading(true);
        this.getDataStorage().storeRevision(value).save().done((response) => {
            if (!response.success) {
                this.Editable.toggleLoading(false);
                this.Editable.setError(response.data.message);
                return;
            }
            this.setValue(response.data);
            this.Editable.setValue(response.data.value).close();
            this.Editable.toggleLoading(false);
            let data = {
                column: this.Cell.getName(),
                item: this.Cell.getObjectID(),
                value: response.data.value
            };
            document.dispatchEvent(new CustomEvent('ACP_InlineEditing_After_Save', { detail: data }));
        });
    }
    openEditable() {
        this.InlineEdit.closeOpenEditables();
        let container = this.getPopperContainer();
        container.append(this.Editable.get());
        this.popper = new Popper(this.getTargetElement(), container, {
            modifiers: {
                arrow: { element: '.ac-edit-popper__caret' }
            }
        });
        this.InlineEdit.openEditable(this.Editable);
        PopperGlobalEvents.bind();
        setTimeout(() => this.Editable.focus(), 200);
    }
    /**
     * Wraps the content of a cell for better control
     */
    wrapCellContent() {
        let settings = this.getEditableSettings();
        let target = document.createElement('span');
        this.Cell.getElement().classList.add(Selectors.CELL, `${Selectors.CELL}--${settings.type}`);
        target.classList.add(Selectors.VALUE);
        while (this.Cell.getElement().firstChild) {
            target.appendChild(this.Cell.getElement().firstChild);
        }
        this.Cell.getElement().append(target);
        let InvalidElements = target.querySelectorAll('.row-actions');
        if (InvalidElements) {
            InvalidElements.forEach((element) => {
                this.Cell.getElement().appendChild(element);
            });
        }
        return target;
    }
    undo() {
        let undo = this.getDataStorage().undo();
        if (undo) {
            this.getDataStorage().save().done((response) => {
                this.setValue(response.data);
                this.Editable.setValue(response.data.value);
            });
        }
    }
    redo() {
        let redo = this.getDataStorage().redo();
        if (redo) {
            this.getDataStorage().save().done((response) => {
                this.setValue(response.data);
                this.Editable.setValue(response.data.value);
            });
        }
    }
    clear() {
        this.getDataStorage().storeRevision('').save().done((response) => {
            this.setValue(response.data);
            this.Editable.setValue(response.data.value);
        });
    }
    setCellValue(value) {
        this.Cell.setValue(value);
    }
    setValue(data) {
        let displayValue = data.display_value;
        if (displayValue) {
            this.Cell.setValue(displayValue);
        }
        else {
            this.getTargetElement().style.backgroundColor = '#f2f093';
            getTableRows([data.id]).then(response => {
                if (response.data.data.table_rows) {
                    this.setCellValue(getColumnValueFromRowHTML(response.data.data.table_rows[data.id], this.Cell.getSettings().name));
                }
            });
        }
    }
    valueUpdateVisual() {
        const el = this.getTargetElement();
        if (el) {
            el.style.animation = 'acp-value-highlight .9s';
        }
    }
    getTargetElement() {
        return this.Cell.getElement().querySelector(`.${Selectors.VALUE}`);
    }
    insertControls() {
        let actions = new Actions(this);
        actions.render();
        if (this.Middleware.hasMediaActions()) {
            let mediaActions = new MediaActions(this);
            mediaActions.render();
        }
        this.moveActions();
    }
    moveActions() {
        const settings = this.getEditableSettings();
        if (!settings.hasOwnProperty('js') || !settings.js.hasOwnProperty('selector')) {
            return;
        }
        let el = this.Cell.getElement().querySelector(settings.js.selector);
        let actions = this.Cell.getElement().querySelector('.acp-ie-controls');
        insertAfter(actions, el);
    }
    afterSetValue() {
        this.wrapCellContent();
        this.valueUpdateVisual();
        this.insertControls();
        this.Cell.getElement().dispatchEvent(new CustomEvent('ACP_InlineEditing_After_SetValue'));
        initAcServices().emitEvent('Edit.Inline.setValue', { cell: this });
    }
    destroyPopper() {
        document.querySelectorAll('.ac-edit-popper').forEach((popper) => {
            popper.remove();
        });
        if (this.popper) {
            this.popper.destroy();
        }
    }
    getPopperContainer() {
        return createPopperContainer();
    }
}
class PopperGlobalEvents {
    static bind() {
        document.addEventListener('keyup', PopperGlobalEvents.escapeEvent);
        document.addEventListener('click', PopperGlobalEvents.clickEvent);
        document.querySelectorAll('.select2-container--acs2').forEach(c => {
            c.addEventListener('click', e => {
                e.stopPropagation();
            });
        });
    }
    static destroy() {
        document.removeEventListener('keyup', PopperGlobalEvents.escapeEvent);
        document.removeEventListener('click', PopperGlobalEvents.clickEvent);
    }
    static escapeEvent(e) {
        if (e.key === "Escape") {
            document.dispatchEvent(new CustomEvent('ACP_InlineEditing_Close'));
        }
    }
    static clickEvent(e) {
        if (e.target === document.body) {
            return;
        }
        if (PopperGlobalEvents.checkInvalidParentClasses(e.target)) {
            return;
        }
        document.dispatchEvent(new CustomEvent('ACP_InlineEditing_Close'));
    }
    static checkInvalidParentClasses(el) {
        let isInvalid = false;
        let invalidClasses = [
            'mce-container',
            'wp-editor-wrap',
            'media-modal',
            'select2-container',
            'ui-datepicker',
            'ui-datepicker-header',
            'ui-datepicker-buttonpane'
        ];
        invalidClasses.map(c => {
            if (el.classList.contains(c))
                isInvalid = true;
        });
        if (isInvalid) {
            return true;
        }
        if (!el.parentElement) {
            return false;
        }
        return PopperGlobalEvents.checkInvalidParentClasses(el.parentElement);
    }
}
const createPopperContainer = () => {
    let element = document.createElement('div');
    element.classList.add('ac-edit-popper');
    element.insertAdjacentHTML('afterbegin', '<span class="ac-edit-popper__caret">');
    document.body.append(element);
    return element;
};
