import { format } from "../../../export/js/helper/string";
import Nanobus from "nanobus";
import { AJAX_VARS } from "ACP/Editing/const";
import axios from "axios";
import { mapDataToFormData } from "AC/helpers/global";
import BulkLogger from "ACP/Editing/bulk-edit/bulk-logger";
import { ErrorLogComponent, NotEditableLogComponent } from "ACP/Editing/bulk-edit/log-component";
const i18n = {
    FINISHED: ACP_Editing.i18n.bulk_edit.feedback.finished,
    UPDATING: ACP_Editing.i18n.bulk_edit.feedback.updating,
    PROCESSED: format(ACP_Editing.i18n.bulk_edit.feedback.processed, '<span data-processed>0</span>', '<span data-total-items></span>'),
    FAIL: ACP_Editing.i18n.bulk_edit.feedback.failure,
    DONE: ACP_Editing.i18n.done,
    DONE_DESELECT: ACP_Editing.i18n.bulk_edit.selecting.done_deselect,
    CANCEL: ACP_Editing.i18n.cancel,
    ERROR: format(ACP_Editing.i18n.bulk_edit.feedback.error, '<span data-errors>0</span>'),
};
export default class Processor {
    constructor(settings, value, items, processAll = false) {
        this.logger = new BulkLogger();
        this.events = new Nanobus();
        this.column = settings;
        this.value = value;
        this.items = items;
        this.totalNumber = Array.isArray(items) ? items.length : items;
        this.numberProcessed = 0;
        this.perPage = ACP_Editing.bulk_edit.updated_rows_per_iteration;
        this.processAll = processAll;
        this.isCanceled = false;
        this.isFinished = false;
        this.timer = {
            seconds: 0,
            minutes: 0,
        };
        this.element = createProcessorElement();
        this.startTimer();
        this.prepareJob();
        this.attachCancelEvent();
        this.updateTotal();
        this.updatePercentage(0);
    }
    getElement() {
        return this.element;
    }
    prepareJob(page = 1) {
        if (!this.processAll) {
            return this.runJob();
        }
        if (page === 1) {
            this.items = [];
        }
        jQuery.ajax({
            url: window.location.href,
            method: 'get',
            data: {
                ac_action: 'get_editable_rows',
                ac_page: page,
                _ajax_nonce: AC.ajax_nonce
            }
        }).done((response) => {
            if (!response.hasOwnProperty('success') || !response.success) {
                return;
            }
            if (!response.data.hasOwnProperty('editable_rows')) {
                return;
            }
            this.items = [].concat(this.items, response.data.editable_rows);
            this.totalNumber = this.items.length;
            if (response.data.rows_per_iteration === response.data.editable_rows.length) {
                this.updateTotal();
                this.prepareJob(page + 1);
                return;
            }
            this.updateTotal();
            this.runJob();
        });
    }
    runJob() {
        if (this.isCanceled) {
            return;
        }
        let self = this;
        let items = self.items;
        let item_set = items.splice(0, self.perPage);
        if (item_set.length === 0) {
            self.onComplete();
            return;
        }
        this.startUpdateThread(item_set).then((response) => {
            if (response.data.success) {
                let num = response.data.data.total;
                self.numberProcessed += num;
                this.updateProcessed(self.numberProcessed);
                this.logger.add(response.data.data.results);
                if (num > 0 && !self.isCanceled) {
                    self.runJob();
                }
                else {
                    self.onComplete();
                }
            }
        });
    }
    startUpdateThread(ids) {
        return axios.post(ajaxurl, mapDataToFormData({
            action: AJAX_VARS.ACTION,
            method: AJAX_VARS.METHOD_BULK_SAVE,
            list_screen: AC.list_screen,
            layout: AC.layout,
            column: this.column.name,
            ids: ids,
            value: this.value,
            _ajax_nonce: AC.ajax_nonce
        }));
    }
    attachCancelEvent() {
        this.element.querySelectorAll('[data-close]').forEach(button => {
            button.addEventListener('click', () => {
                if (this.isFinished) {
                    button.remove();
                    this.events.emit('close', {});
                }
                else {
                    this.isCanceled = true;
                    this.onComplete();
                }
            });
        });
    }
    updateTotal() {
        this.element.querySelector('[data-total-items]').innerHTML = this.totalNumber.toString();
    }
    updateProcessed(number) {
        this.element.querySelector('[data-processed]').innerHTML = number.toString();
        let percentage = Math.round((number / this.totalNumber) * 100);
        this.updatePercentage(percentage);
    }
    updatePercentage(percentage) {
        this.element.querySelector('[data-percentage]').innerHTML = `${percentage}%`;
        this.element.querySelector('[data-status-percentage]').style.width = `${percentage}%`;
    }
    onComplete() {
        if (!this.isCanceled) {
            this.element.querySelector('.acp-be-progress__process').innerHTML = format(i18n.FINISHED, this.totalNumber) + ' (100%) <span class="dashicons dashicons-yes"></span>';
        }
        this.element.querySelector('[data-close]').innerHTML = i18n.DONE;
        this.element.querySelector('[data-close]').classList.add('button-primary');
        this.element.querySelector('[data-status-percentage]').classList.add('-finished');
        this.isFinished = true;
        this.stopTimer();
        this.canDismiss = false;
        this.addDeselectDoneButton();
        if (this.logger.hasError()) {
            const Log = new ErrorLogComponent(this.logger.getErrors());
            this.element.querySelector('[data-status-percentage]').classList.add('-error');
            this.element.querySelector('.errors').append(Log.element);
        }
        if (this.logger.hasNotEditable()) {
            const Log = new NotEditableLogComponent(this.logger.getNotEditable());
            this.element.querySelector('[data-status-percentage]').classList.add('-error');
            this.element.querySelector('.errors').append(Log.element);
        }
        this.events.emit('finished');
    }
    addDeselectDoneButton() {
        let button = createDoneButton();
        button.addEventListener('click', (e) => {
            e.preventDefault();
            AC_SERVICES.getService('Table').getElement().querySelectorAll('.check-column input[type=checkbox]:checked').forEach(cb => {
                cb.checked = false;
                cb.dispatchEvent(new Event('change'));
            });
            this.events.emit('close');
        });
        this.element.append(button);
    }
    startTimer() {
        this.addSecond();
        this.timerInterval = window.setInterval(() => {
            this.addSecond();
        }, 1000);
    }
    stopTimer() {
        clearInterval(this.timerInterval);
    }
    addSecond() {
        let element = this.element.querySelector('[data-timer]');
        this.timer.seconds++;
        if (this.timer.seconds >= 60) {
            this.timer.seconds = 0;
            this.timer.minutes++;
        }
        element.textContent = (this.timer.minutes ? (this.timer.minutes > 9 ? this.timer.minutes : "0" + this.timer.minutes) : "00") + ":" + (this.timer.seconds > 9 ? this.timer.seconds : "0" + this.timer.seconds);
    }
}
const createDoneButton = () => {
    let button = document.createElement('button');
    button.classList.add('button', 'done-deselect');
    button.innerText = i18n.DONE_DESELECT;
    button.dataset.close = 'true';
    return button;
};
const ProcessTemplate = `
		<div class="acp-be-progress">
			<div class="acp-be-progress__timer" data-timer></div>
			<div class="acp-be-progress__process">${i18n.PROCESSED} (<span data-percentage></span>)</div>
		</div>

		<div class="acp-be-statusbar">
			<div class="acp-be-statusbar__progress" data-status-percentage></div>
		</div>

		<div class="errors">
			
		</div>

		<button data-close class="button">Cancel</button>
`;
const createProcessorElement = () => {
    let element = document.createElement('div');
    element.classList.add('acp-be-processing');
    element.innerHTML = ProcessTemplate;
    return element;
};
