import AcHtmlElement from "AC/helpers/html-element";
import { format } from "../../../export/js/helper/string";
const i18n = {
    ERROR: format(ACP_Editing.i18n.bulk_edit.feedback.error, '<span  class="ac-bulk-log__status__count"><span data-errors-count>0</span> errors</span>'),
    NOT_EDITABLE: format(ACP_Editing.i18n.bulk_edit.feedback.error, '<span  class="ac-bulk-log__status__count -medium"><span data-errors-count>0</span> unmodifiable items</span>'),
    SHOW_ITEMS: ACP_Editing.i18n.bulk_edit.feedback.show_items,
    HIDE_ITEMS: ACP_Editing.i18n.bulk_edit.feedback.hide_items,
    NOT_EDITABLE_FOUND: ACP_Editing.i18n.bulk_edit.feedback.not_editable_found,
};
class LogComponent {
    constructor(items, error) {
        this.items = items;
        this.element = AcHtmlElement.create('div').addClass('bulk-log').addHtml(template(error)).element;
        this.addItems(items);
        this.element.querySelector('[data-errors-count]').innerHTML = items.length.toString();
        this.element.querySelector('[data-toggle-errors]').addEventListener('click', () => {
            this.toggleItems();
        });
    }
    toggleItems() {
        const link = this.element.querySelector('[data-toggle-errors]');
        const items = this.element.querySelector('[data-log-items]');
        const toggleClass = '-visible';
        if (items.classList.contains(toggleClass)) {
            items.classList.remove(toggleClass);
            link.innerHTML = i18n.SHOW_ITEMS;
        }
        else {
            items.classList.add(toggleClass);
            link.innerHTML = i18n.HIDE_ITEMS;
        }
    }
}
export class ErrorLogComponent extends LogComponent {
    constructor(items) {
        super(items, i18n.ERROR);
    }
    addItems(items) {
        let container = this.element.querySelector('[data-log-items]');
        items.forEach(item => {
            container.append(AcHtmlElement.create('div').addHtml(`ID: ${item.id} - ${item.error}`).element);
        });
    }
}
export class NotEditableLogComponent extends LogComponent {
    constructor(items) {
        super(items, i18n.NOT_EDITABLE);
    }
    addItems(items) {
        let ids = items.map(item => item.id).join(', ');
        this.element.querySelector('[data-log-items]').innerHTML = `${i18n.NOT_EDITABLE_FOUND}<br>ID: ${ids}`;
    }
}
const template = (error) => {
    return `
     <div class="ac-bulk-log">
        <div class="ac-bulk-log__status">
            <span>${error}</span>
            <a data-toggle-errors>${i18n.SHOW_ITEMS}</a>
        </div>
        <div class="ac-bulk-log__items" data-log-items>
        
        </div>
     </div>
    `;
};
