<script lang="ts">
    import axios from "axios";

    declare const ajaxurl: string;

    type postInfo = {
        id: number,
        title: string,
        permalink: string,
        post_type: string
    }

    export let value: string;
    export let onChange: Function;

    let items: Array<postInfo> = [];

    let CancelToken = axios.CancelToken;
    let CancelSource = CancelToken.source();

    const getPermalinks = (search: string) => {
        CancelToken = axios.CancelToken;
        CancelSource = CancelToken.source();

        return axios.get(ajaxurl, {
            cancelToken: CancelSource.token,
            params: {
                search: search,
                action: 'acp-permalinks'
            }
        });
    }

    const isUrl = (value: string): boolean => {
        return (value.match(/http:|https:|ftp:|mailto:/) !== null);
    }

    const emitChange = () => {
        if (onChange) {
            onChange(value);
        }
    }

    const onClick = (permalink: string) => {
        value = permalink;
        items = [];
        emitChange();
    }

    const onInput = () => {
        emitChange();
        CancelSource.cancel();

        if (isUrl(value)) {
            items = [];
            return;
        }

        getPermalinks(value).then(d => {
            items = d.data.data;
        }).catch( () => {

        } );
    }

    const init = ( el: HTMLElement ) => {
        setTimeout(() =>{
            el.focus()
		},200);
	}

</script>

<div class="">
	<input bind:value={value} on:keyup={onInput} type="url" use:init>
	{#if items.length > 0}
		<div class="acp-post-list">
			<ul class="acp-post-list__list">
				{#each items as item}
					<li class="acp-post-list__item" on:click={()=>{ onClick(item.permalink)}}>
						<span class="acp-post-list__item__title">{item.title}</span>
						<span class="acp-post-list__item__type">{item.post_type}</span></li>
				{/each}
			</ul>
		</div>
	{/if}
</div>