import EditableTemplate from '../templates/editable';
import Nanobus from "nanobus";
import { getNestedValue, parseArguments } from "ACP/Editing/helpers/object";
export default class AbstractEditable {
    constructor(args) {
        this.isInit = false;
        this.template = new EditableTemplate();
        this.settings = parseArguments(args, this.getDefaults());
        this.Events = new Nanobus();
    }
    close() {
        this.removeElement();
        this.Events.emit('close', this);
        return this;
    }
    focus() {
        return;
    }
    get() {
        this.element = this.template.getElement();
        this.beforeRender();
        this.render();
        this.valueToInput(this.settings.value);
        this.initEvents();
        return this.element;
    }
    render() {
        return;
    }
    getEditableTemplate() {
        return this.template;
    }
    toggle(enabled) {
        this.element.querySelectorAll('input, select, textarea, button').forEach(input => input.disabled = (enabled === false));
        this.disabled = (enabled === false);
        return this;
    }
    disable() {
        return this.toggle(false);
    }
    enable() {
        return this.toggle(true);
    }
    beforeRender() {
        this.template
            .setTemplate(this.getTemplate()).addClass('-' + this.getEditableType()).setError('')
            .showButtons(!this.settings.hasOwnProperty('showbuttons') || this.settings.showbuttons !== false);
    }
    save() {
        if (!this.disabled && this.validate()) {
            this.Events.emit('save', this);
        }
    }
    initEvents() {
        if (this.isInit) {
            return;
        }
        this.getElement().addEventListener('click', (e) => e.stopPropagation());
        this.getElement().querySelector('button[data-submit]').addEventListener('click', (e) => {
            e.preventDefault();
            this.save();
        });
        this.getElement().querySelector('button[data-cancel]').addEventListener('click', (e) => {
            e.preventDefault();
            this.close();
        });
        this.getElement().querySelector('form').addEventListener('submit', (e) => {
            e.preventDefault();
            this.save();
        });
        this.isInit = true;
    }
    getArg(selector, defaultValue = null) {
        return getNestedValue(this.settings, defaultValue, selector.split('.'));
    }
    removeElement() {
        if (this.getElement()) {
            this.getElement().remove();
        }
    }
    toggleLoading(loading = true) {
        if (!this.getElement()) {
            return;
        }
        loading
            ? this.getElement().classList.add('-loading')
            : this.getElement().classList.remove('-loading');
    }
    setValue(value) {
        this.settings.value = value;
        return this;
    }
    setObjectId(objectId) {
        this.settings.object_id = objectId;
        return this;
    }
    getObjectId() {
        return this.settings.hasOwnProperty('object_id') ? this.settings.object_id : false;
    }
    setError(error) {
        this.template.setError(error);
    }
    getElement() {
        return this.element;
    }
    validate() {
        let form = this.getElement().querySelector('form');
        if (!this.getElement().querySelector('form').checkValidity()) {
            form.reportValidity();
            return false;
        }
        return true;
    }
    getDefaults() {
        return {
            title: false,
            showbuttons: true,
            mode: 'inline'
        };
    }
}
