import AbstractEditable from './abstract';
export default class ColorEditable extends AbstractEditable {
    getEditableType() {
        return "color";
    }
    focus() {
        this.getElement().querySelector('button').focus({ preventScroll: true });
    }
    valueToInput(value) {
        let input = this.getElement().querySelector('input');
        if (value && input) {
            input.value = value;
        }
        jQuery(input).wpColorPicker();
    }
    getValue() {
        return this.getElement().querySelector('input').value;
    }
    getTemplate() {
        return this.getEditableTemplate().getFormHelper().input('color').outerHTML;
    }
}
