import AbstractEditable from './abstract';
import { evalValue } from "ACP/Editing/helpers/object";
export default class FullnameEditable extends AbstractEditable {
    getEditableType() {
        return "fullname";
    }
    focus() {
        this.getFirstNameElement().focus({ preventScroll: true });
    }
    render() {
        this.getFirstNameElement().placeholder = this.getArg('placeholder_first_name', '');
        this.getLastNameElement().placeholder = this.getArg('placeholder_first_name', '');
    }
    valueToInput(values) {
        this.getFirstNameElement().value = evalValue(values.first_name, '');
        this.getLastNameElement().value = evalValue(values.last_name, '');
    }
    getValue() {
        return {
            'first_name': this.getFirstNameElement().value,
            'last_name': this.getLastNameElement().value,
        };
    }
    getTemplate() {
        return `
			<div class="aceditable__form__inputs">
				<div class="input__group">
				<input name="first_name" type="text" placeholder="First name">
				<input name="last_name" type="text" placeholder="Last name">
				</div>
			</div>
		`;
    }
    getFirstNameElement() {
        return this.getElement().querySelector('[name="first_name"]');
    }
    getLastNameElement() {
        return this.getElement().querySelector('[name="last_name"]');
    }
}
