import AbstractEditable from './abstract';
const CLASSES = {
    POPWRAP: 'ac-edit-popper-wrapper'
};
export default class WysiwygEditable extends AbstractEditable {
    getEditableType() {
        return "wysiwyg";
    }
    render() {
        let id = this.getEditorId();
        let $el = jQuery(this.getElement().querySelector('textarea'));
        $el.attr('id', id);
        this.getElement().addEventListener('click', e => e.preventDefault());
        setTimeout(() => {
            let settings = this.getEditorSettings();
            let popper = this.getElement().closest('.ac-edit-popper');
            let modal = this.getElement().closest('.ac-modal.-bulkedit');
            // Inline Edit specific
            if (popper) {
                popper.classList.add('-full');
                this.wrapPopper();
            }
            // Bulk Edit specific
            if (modal) {
                modal.classList.add('-wysiwyg');
                settings.tinymce.height = '300px';
            }
            wp.editor.initialize(id, settings);
        });
    }
    wrapPopper() {
        let wrapper = document.createElement('div');
        let popper = this.getElement().closest('.ac-edit-popper');
        wrapper.classList.add(CLASSES.POPWRAP);
        document.body.append(wrapper);
        wrapper.appendChild(popper);
    }
    removePopperwrap() {
        document.querySelectorAll(`.${CLASSES.POPWRAP}`).forEach(el => el.remove());
    }
    getEditorSettings() {
        let settings = Object.assign({}, wp.editor.getDefaultSettings());
        settings.tinymce.toolbar1 = 'formatselect,bold,italic,bullist,numlist,blockquote,alignleft,alignright,aligncenter,link,unlink,wp_add_media,wp_more,fullscreen,wp_adv';
        settings.tinymce.toolbar2 = 'strikethrough,hr,forecolor,pastetext,removeformat,charmap,undo,redo';
        settings.tinymce.width = '100%';
        settings.quicktags = true;
        settings.mediaButtons = true;
        return settings;
    }
    close() {
        this.id = null;
        this.removePopperwrap();
        return super.close();
    }
    getEditorId() {
        if (!this.id) {
            this.id = 'acp_' + Math.floor(Math.random() * 10000) + '_' + Math.floor(Math.random() * 10000) + '_' + Math.floor(Math.random() * 10000);
        }
        return this.id;
    }
    valueToInput(value) {
        let textarea = this.getElement().querySelector('textarea');
        if (value && textarea) {
            textarea.value = value;
        }
    }
    getValue() {
        let id = this.getEditorId();
        return wp.editor.getContent(id);
    }
    getTemplate() {
        return `<textarea></textarea>`;
    }
}
