import { mapDataToFormData } from "AC/helpers/global";
import axios from "axios";
import { AJAX_VARS } from "ACP/Editing/const";
export function getEditableValues(ids) {
    return axios.post(ajaxurl, mapDataToFormData({
        action: AJAX_VARS.ACTION,
        method: AJAX_VARS.METHOD_INLINE_VALUES,
        ids: ids,
        list_screen: AC.list_screen,
        layout: AC.layout,
        _ajax_nonce: AC.ajax_nonce
    }));
}
export function persistEditabilityState(enabled) {
    return axios.post(ajaxurl, mapDataToFormData({
        action: AJAX_VARS.ACTION,
        method: AJAX_VARS.METHOD_EDIT_STATE,
        value: enabled ? 1 : 0,
        list_screen: AC.list_screen,
        layout: AC.layout,
        _ajax_nonce: AC.ajax_nonce
    }));
}
export function getTableRows(ids) {
    return axios.post(window.location.href, mapDataToFormData({
        ac_action: 'get_table_rows',
        ac_ids: ids,
        _ajax_nonce: AC.ajax_nonce
    }));
}
