export default class Abstract {
    constructor(editables, EditableSettings, column, mode) {
        this.Editables = editables;
        this.settings = EditableSettings;
        this.column = column;
        this.mode = mode;
        this.args = {
            mode: mode,
        };
        this.map();
    }
    getEditable() {
        return null;
    }
    getArgs() {
        return this.args;
    }
    hasMediaActions() {
        return false;
    }
    map() {
    }
}
