import ColumnDataStorage from "../modules/data-storage";
import { EDIT_SERVICES } from "ACP/Editing/const";
export const CellMiddleware = (model, config) => {
    config.forEach(value => {
        let cell = model.get(value.id, value.column_name);
        let settings = cell.getSettings().getService(EDIT_SERVICES.INLINE_EDITABLE);
        if (settings === null) {
            return;
        }
        if (settings.hasOwnProperty('store_values') && settings.store_values === false) {
            value.formatted_value = value.value;
            value.value = typeof value === 'object' ? Object.keys(value.value) : '';
        }
        if (isSingleValueConfig(settings)) {
            value.formatted_value = value.value;
            value.value = Object.keys(value.value)[0];
        }
        cell.setService('dataStorage', new ColumnDataStorage(value));
    });
};
const isSingleValueConfig = (settings) => {
    if (settings.hasOwnProperty('store_single_value') && settings.store_single_value) {
        return true;
    }
    return settings.type === 'select2_dropdown' && (!settings.hasOwnProperty('multiple') || settings.multiple === false);
};
