import AbstractEditable from './abstract';
export default class SelectEditable extends AbstractEditable {
    getEditableType() {
        return "select";
    }
    focus() {
        this.getElement().querySelector('select').focus({ preventScroll: true });
    }
    render() {
        let select = this.getElement().querySelector('select');
        Object.keys(this.settings.options).forEach((k) => {
            let option = this.settings.options[k];
            select.append(new Option(option.label, option.value));
        });
    }
    getSelectElement() {
        return this.getElement().querySelector('select[name="value"]');
    }
    valueToInput(value) {
        if (!value) {
            return;
        }
        this.getSelectElement().value = value;
    }
    getValue() {
        return this.getSelectElement().value;
    }
    getTemplate() {
        return `<select name="value"></select>`;
    }
}
