import AbstractEditable from './abstract';
const i18n = {
    REPLACE: ACP_Editing.i18n.replace_with,
    ADD: ACP_Editing.i18n.operators.add,
    SUBTRACT: ACP_Editing.i18n.operators.subtract,
    MULTIPLY: ACP_Editing.i18n.operators.multiply,
    DIVIDE: ACP_Editing.i18n.operators.divide
};
const COMPUTATION = {
    REPLACE: 'replace',
    ADD: 'add',
    SUBTRACT: 'subtract',
    MULTIPLY: 'multiply',
    DIVIDE: 'divide'
};
export default class NumberExtended extends AbstractEditable {
    render() {
        this.initValidationAttributes();
        this.getValueInput().addEventListener('blur', () => {
            this.initValidationAttributes();
        });
    }
    focus() {
        this.getElement().querySelector('input').focus();
    }
    getEditableType() {
        return "number";
    }
    valueToInput(value) {
        if (value) {
            this.getElement().querySelector('input').value = value;
        }
    }
    getValueInput() {
        return this.getElement().querySelector('[name="value"]');
    }
    getComputationSelect() {
        return this.getElement().querySelector('[name=computation_type]');
    }
    getComputationType() {
        return this.getComputationSelect().value;
    }
    initValidationAttributes() {
        const type = this.getComputationType();
        let input = this.getValueInput();
        input.required = type !== COMPUTATION.REPLACE;
    }
    getValue() {
        return {
            allow_negative: true,
            value: this.getValueInput().value,
            computation_type: this.getComputationType()
        };
    }
    getTemplate() {
        let input = this.getEditableTemplate().getFormHelper().input('value', null, this.settings.html_attributes).outerHTML;
        return `
            <div class="input__group -type -strategy">
                <select class="type" name="computation_type">
                    <option value="${COMPUTATION.REPLACE}">${i18n.REPLACE}..</option>
                    <option value="${COMPUTATION.ADD}">${i18n.ADD}..</option>
                    <option value="${COMPUTATION.SUBTRACT}">${i18n.SUBTRACT}..</option>
                    <option value="${COMPUTATION.MULTIPLY}">${i18n.MULTIPLY}..</option>
                    <option value="${COMPUTATION.DIVIDE}">${i18n.DIVIDE}..</option>
                </select>
                ${input}
            </div>
        `;
    }
}
