import AbstractEditable from './abstract';
export default class CheckboxEditable extends AbstractEditable {
    getEditableType() {
        return "checkbox";
    }
    focus() {
        let select = this.getElement().querySelector('form input[type=checkbox]');
        if (select) {
            select.focus({ preventScroll: true });
        }
    }
    render() {
        const group = this.getElement().querySelector('.input__group.-checkbox');
        Object.keys(this.settings.options).forEach((k) => {
            let option = this.settings.options[k];
            group.insertAdjacentHTML('beforeend', this.getEditableTemplate().getFormHelper().checkbox('option[]', option.value, option.text).outerHTML);
        });
    }
    valueToInput(values) {
        if (Array.isArray(values)) {
            values.forEach((value) => {
                let input = this.getElement().querySelector(`input[value="${value}"]`);
                if (input) {
                    input.checked = true;
                }
            });
        }
    }
    getValue() {
        let values = [];
        this.getElement().querySelectorAll('form input[type=checkbox]:checked').forEach((c) => values.push(c.value));
        return values;
    }
    getTemplate() {
        return `<div class="input__group -checkbox"></div>`;
    }
    getDefaults() {
        let defaults = super.getDefaults();
        defaults['options'] = [];
        return defaults;
    }
}
