import ColumnDataStorage from "ACP/Editing/modules/data-storage";
import { getEditableValues, persistEditabilityState } from "ACP/Editing/helpers/ajax";
import IECell from './inline-edit-cell';
import { getIdFromTableRow, getRowCellByName } from "AC/helpers/table";
import { EDIT_SERVICES } from "ACP/Editing/const";
export default class InlineEdit {
    constructor(Table, Editables, EditableMiddleware, enabled, version) {
        this.Editables = Editables;
        this.Table = Table;
        this.Middleware = EditableMiddleware;
        this.enabled = enabled;
        this.version = version;
        this.initialized = false;
        this.openEditables = [];
        this.setTableClass();
        this.addEvents();
        this.initCells();
        this.setInputState();
    }
    isEnabled() {
        return this.enabled;
    }
    getVersion() {
        return this.version;
    }
    enable() {
        this.enabled = true;
        this.storePreference();
        this.setTableClass();
        this.initCells();
        this.setInputState();
    }
    setInputState() {
        document.querySelectorAll('#acp-enable-editing').forEach(input => input.checked = this.isEnabled());
    }
    disable() {
        this.enabled = false;
        this.storePreference();
        this.setTableClass();
    }
    setInitialised(initialized = true) {
        this.initialized = initialized;
        return this;
    }
    isInitialised() {
        return this.initialized;
    }
    setTableClass() {
        let table = this.Table.getElement();
        table.classList.add('acp-ie-table', 'acp-ie-table--' + this.getVersion());
        if (this.isEnabled()) {
            table.classList.add('acp-ie-enabled');
            table.classList.remove('acp-ie-disabled');
        }
        else {
            table.classList.add('acp-ie-disabled');
            table.classList.remove('acp-ie-enabled');
        }
    }
    initCell(cell) {
        const settings = cell.getSettings();
        const editSettings = settings.getService(EDIT_SERVICES.INLINE_EDITABLE);
        if (!settings || !editSettings || !editSettings.type || cell.hasService('IEdit')) {
            return;
        }
        const middleware = this.Middleware.getInlineEditable(settings);
        const editable = middleware.getEditable();
        if (editable && cell.getService(EDIT_SERVICES.DATA_STORAGE)) {
            cell.setService('IEdit', new IECell(this, cell, middleware, editable));
        }
    }
    initRow(id) {
        getEditableValues([id]).then(response => {
            response.data.data.editable_values.forEach(item => {
                let cell = this.Table.Cells.get(item.id, item.column_name);
                if (!cell.hasService('dataStorage')) {
                    cell.setService('dataStorage', new ColumnDataStorage({
                        column_name: cell.getName(),
                        id: cell.getObjectID()
                    }));
                }
                this.initCell(cell);
                if (cell.hasService('IEdit') && cell.getService('IEdit').hasOwnProperty('Editable')) {
                    cell.getService('IEdit').Editable.setValue(item.value);
                }
            });
        });
    }
    updateRowCells(row) {
        Object.keys(this.Table.Columns.getColumns()).forEach((column_name) => {
            let cell = this.Table.Cells.get(getIdFromTableRow(row), column_name);
            if (cell) {
                cell.setElement(getRowCellByName(row, column_name));
                if (cell.hasService('IEdit')) {
                    cell.getService('IEdit').afterSetValue();
                }
            }
        });
    }
    openEditable(editable) {
        this.openEditables.push(editable);
    }
    closeOpenEditables() {
        this.openEditables.forEach((editable) => {
            editable.close();
        });
        this.openEditables = [];
    }
    initCells() {
        if (!this.isInitialised() && this.isEnabled()) {
            this.Table.Cells.getAll().forEach(cell => this.initCell(cell));
            this.setInitialised();
        }
    }
    storePreference() {
        persistEditabilityState(this.isEnabled());
    }
    addEvents() {
        document.querySelectorAll('#acp-enable-editing').forEach(input => {
            input.addEventListener('change', () => input.checked ? this.enable() : this.disable());
        });
        document.addEventListener('ACP_InlineEditing_Close', () => this.closeOpenEditables());
        jQuery(this.Table.getElement()).on('updated', 'tr', (e) => this.updateRowCells(e.target));
        document.addEventListener('keydown', (e) => {
            if (e.key === 'Escape' || e.key === 'Esc') {
                this.closeOpenEditables();
            }
        }, true);
    }
}
